import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { AppSettings } from '../app.constant';

@Injectable()
export class CategoryService {
    constructor(private http: Http) { }

    getAll(sort: string, page: any, amount: any, keysearch: string) {
        return this.http.get(AppSettings.API_ENDPOINT + 'categories/GetAll?sort=' + sort + '&page=' + page + '&amount=' + amount + '&keysearch=' + keysearch)
        .map((response: Response) => response.json());
    }

    create(category: any) {
        return this.http.post(AppSettings.API_ENDPOINT +'categories/Create', category, this.jwt()).map((response: Response) => response.json());
    }

    update(category: any) {
        return this.http.put(AppSettings.API_ENDPOINT +'categories/Update', category, this.jwt()).map((response: Response) => response.json());
    }

    delete(id: any) {
        return this.http.delete(AppSettings.API_ENDPOINT +'categories/Delete?Id=' + id, this.jwt()).map((response: Response) => response);
    }

    getAllForProduct(){
        return this.http.get(AppSettings.API_ENDPOINT + 'categories/GetAllForProduct')
        .map((response: Response) => response.json());
    }

    // private helper methods

    private jwt() {
        let headers = new Headers({ 
            'Content-Type' : 'application/json'
        });
        return new RequestOptions({ headers: headers });
    }
}


