import { Component, keyframes, ViewContainerRef } from '@angular/core';
import { Router } from '@angular/router';
import { CategoryService, PagerService } from '../_services/index';
import * as _ from 'underscore';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
declare var jquery: any;
declare var $: any;

@Component({
    moduleId: module.id,
    templateUrl: 'categories.component.html'
})

export class CategoriesComponent {
    categories: any = [];
    sort: string; page: any; amount: any;
    category: any = {};
    nameBtnCreate: string;
    idUpdate: any;
    isUpdating: boolean;
    allDatas: any = {};
    pager: any = {};
    pagedItems: any[];
    keysearch: string;
    gopage: string;

    constructor(private router: Router, private categoryService: CategoryService, 
        private pagerService: PagerService, 
        public toastr: ToastsManager, vcr: ViewContainerRef) {
        this.toastr.setRootViewContainerRef(vcr);
        this.sort = 'Name';
        this.page = 0;
        this.amount = 10;
        this.nameBtnCreate = 'Tạo';
        this.isUpdating = false;
        this.keysearch = '';
    }

    ngOnInit() {
        $('#btn-cancel').hide();
        this.loadAllCategories(this.sort, this.page, this.amount, this.keysearch);
    }

    private loadAllCategories(sort: string, page: any, amount: any, keysearch: any) {
        this.categoryService.getAll(sort, page, amount, keysearch).subscribe(data => {
            this.categories = data.Data;
            this.pagedItems = data.Data;
            this.allDatas = data;
            this.pager = this.pagerService.getPager(this.allDatas.TotalPage, this.allDatas.TotalRecord, page, this.amount);
            this.page = page;
        });
    }

    setPage(page: number) {
        if (page < 1 || page > this.pager.totalPages) {
            return;
        }
        this.loadAllCategories(this.sort, page - 1, this.amount, this.keysearch);
    }

    CreateCategory() {
        if (this.isUpdating) {
            this.category.Id = this.idUpdate;
            this.categoryService.update(this.category).subscribe(data => {
                this.loadAllCategories(this.sort, this.page, this.amount, this.keysearch);
                this.category.Name = '';
                this.category.Description = '';
                this.isUpdating = false;
                this.nameBtnCreate = 'Tạo';
                $('#btn-cancel').hide();
                this.toastr.success('Cập nhật loại sản phẩm thành công!', 'Thành Công!');
            });
        } else {
            this.categoryService.create(this.category).subscribe(data => {
                this.loadAllCategories(this.sort, this.page, this.amount, this.keysearch);
                this.category.Name = '';
                this.category.Description = '';
                this.toastr.success('Tạo loại sản phẩm thành công!', 'Thành Công!');
            });
        }
    }

    UpdateCategory(item: any) {
        this.category.Name = item.Name;
        this.category.Description = item.Description;
        this.nameBtnCreate = 'Sửa';
        this.idUpdate = item.Id;
        this.isUpdating = true;
        $('#btn-cancel').show();
    }

    cancelUpdate() {
        this.category.Name = '';
        this.category.Description = '';
        this.nameBtnCreate = 'Tạo';
        this.idUpdate = '';
        this.isUpdating = false;
        $('#btn-cancel').hide();
    }

    DeteleCategory(id: any) {
        this.categoryService.delete(id).subscribe(data => {
            this.loadAllCategories(this.sort, this.page, this.amount, this.keysearch);
            this.toastr.info('Xóa loại sản phẩm thành công!', 'Thành Công!');
        });
    }

    gotoPage() {
        if (this.gopage != "") {
            this.setPage(+this.gopage);
        } else {
            this.setPage(1);
        }
    }

    goSearch() {
        this.setPage(1);
    }

}



