import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/index';
import { LoginComponent } from './login/index';
import { AuthGuard } from './_guards/index';
import { CategoriesComponent } from './categories/index';
import { ProductsComponent } from './products/index';
import { CreateProductComponent } from './create-product/index';
import { ProductDetailComponent } from './product-detail/index';

const appRoutes: Routes = [
    { path: '', component: HomeComponent, canActivate: [AuthGuard] },
    { path: 'login', component: LoginComponent },
    { path: 'categories', component: CategoriesComponent },
    { path: 'products', component: ProductsComponent },
    { path: 'create-product', component: CreateProductComponent },
    {path: 'product-detail/:id', component: ProductDetailComponent },
    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

export const routing = RouterModule.forRoot(appRoutes);


