import { Component, OnInit, ViewContainerRef } from '@angular/core';
import { ProductService, CategoryService } from '../_services/index';
import { ActivatedRoute } from '@angular/router';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { unescape } from 'querystring';
import { forEach } from '@angular/router/src/utils/collection';
import { AppSettings } from '../app.constant';
declare var jquery: any;
declare var $: any;
declare var CKEDITOR: any;
@Component({
    moduleId: module.id.toString(),
    templateUrl: 'product-detail.component.html',
    styleUrls: [
        'product-detail.css'
    ],
})

export class ProductDetailComponent implements OnInit {
    product: any;
    private id: any;
    categories: any = [];
    loadedContent: boolean;

    constructor(
        private productService: ProductService, private categoryService: CategoryService,
        private route: ActivatedRoute, public toastr: ToastsManager, vcr: ViewContainerRef) {

        this.toastr.setRootViewContainerRef(vcr);
        this.loadedContent = false;
    }

    ngOnInit() {
        this.id = this.route.snapshot.params['id'];
        this.loadAllCatagories();
        this.getInfoProduct(this.id);
    }

    private loadAllCatagories() {
        this.categoryService.getAllForProduct().subscribe(data => {
            this.categories = data;
            setTimeout(function(){
                $('#select2-categories-detail').select2();
            }, 100);
        });
    }

    private getInfoProduct(id: string) {
        this.productService.getById(id).subscribe(product => {
            product.Description = $('<div/>').html(product.Description).text();;
            this.product = product;
            this.loadedContent = true;
            /// set value selected categories
            var selectedCates: any = [];
            this.product.Categories.forEach(function(e: any){
                selectedCates.push(e.Id);
            });
            setTimeout(function(){
                $('#select2-categories-detail').select2().val(selectedCates).trigger('change');
            }, 100);
            this.product.ProductImages.forEach(function(image: any){
                image.Path = AppSettings.API_ENDPOINT_IMAGE + image.Path.substr(1);   
            });
        });
    }

    // deleteUser(id: number) {
    //     this.userService.delete(id).subscribe(() => { this.loadAllUsers() });
    // }

    // private loadAllUsers() {
    //     this.userService.getAll().subscribe(users => { this.users = users; });
    // }
}


