import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { AppSettings } from '../app.constant';


@Injectable()
export class ProductService {
    constructor(private http: Http) { }

    getAll(sort: string, page: any, amount: any, keysearch: string) {
        return this.http.get(AppSettings.API_ENDPOINT + 'products/GetAll?sort=' + sort + '&page=' + page + '&amount=' + amount + '&keysearch=' + keysearch)
            .map((response: Response) => response.json());
    }

    create(formData: FormData) {
        return this.http.post(AppSettings.API_ENDPOINT + 'products/Create', formData, this.jwtFormData()
        ).map((response: Response) => response.json());
    }

    update(product: any) {
        return this.http.put(AppSettings.API_ENDPOINT + 'products/Update', product, this.jwt()).map((response: Response) => response.json());
    }

    delete(id: any) {
        return this.http.delete(AppSettings.API_ENDPOINT + 'products/Delete?Id=' + id, this.jwt()).map((response: Response) => response);
    }

    getById(id: string) {
        return this.http.get(AppSettings.API_ENDPOINT + 'products/getById?Id=' + id, this.jwt()).map((response: Response) => response.json());
    }
    // private helper methods

    private jwt() {
        let headers = new Headers({
            'Content-Type': 'application/json'
        });
        return new RequestOptions({ headers: headers });
    }

    private jwtFormData() {
        var headers = new Headers();
        headers.append('Content-Type', 'multipart/form-data');
        headers.append('Accept', 'application/json');
        let options = new RequestOptions({ headers: headers });
        return options;
    }
    getFormUrlEncoded(toConvert: any) {
        const formBody = [];
        for (const property in toConvert) {
            const encodedKey = encodeURIComponent(property);
            const encodedValue = encodeURIComponent(toConvert[property]);
            formBody.push(encodedKey + '=' + encodedValue);
        }
        return formBody.join('&');
    }

}


