import { Component, keyframes } from '@angular/core';
import { Router } from '@angular/router';
import { CategoryService, PagerService, ProductService } from '../_services/index';
import * as _ from 'underscore';
declare var jquery:any;
declare var $ :any;
import { AppSettings } from '../app.constant';
import { forEach } from '@angular/router/src/utils/collection';
import {SlimLoadingBarService} from 'ng2-slim-loading-bar';

@Component({
    moduleId: module.id,
    templateUrl: 'products.component.html'
})

export class ProductsComponent {
    products: any = [];
    sort: string; page: any; amount: any;
    category: any = {};
    nameBtnCreate: string;
    allDatas: any = {};
    pager: any = {};
    pagedItems: any[];
    keysearch: string;
    gopage: string;

    constructor(private router: Router, private categoryService: CategoryService, 
        private pagerService: PagerService,
        private productService: ProductService, private slimLoadingBarService: SlimLoadingBarService) {
        this.sort = 'Name';
        this.page = 0;
        this.amount = 10;
        this.keysearch = '';
    }

    ngOnInit() {
        this.loadAllProducts(this.sort, this.page, this.amount, this.keysearch);
    }

    private loadAllProducts(sort: string, page: any, amount: any, keysearch: any) {
        this.slimLoadingBarService.start(() => {
            console.log('Loading complete');
        });
        this.productService.getAll(sort, page, amount, keysearch).subscribe(data => {
            data.Data.forEach(function(e: any){
                e.ProductImages.forEach(function(image: any){
                    image.Path = AppSettings.API_ENDPOINT_IMAGE + image.Path.substr(1);
                });    
            });
            this.products = data.Data;
            this.pagedItems = data.Data;
            this.allDatas = data;
            this.pager = this.pagerService.getPager(this.allDatas.TotalPage, this.allDatas.TotalRecord, page, this.amount);
            this.page = page;
            this.slimLoadingBarService.complete();
        });
    }

    setPage(page: number) {
        if (page < 1 || page > this.pager.totalPages) {
            return;
        }
        this.loadAllProducts(this.sort, page - 1, this.amount, this.keysearch);
    }


    detailProduct(id: string) {
        this.router.navigateByUrl('/product-detail/' + id);
    }

    DeteleCategory(id: any) {
        this.categoryService.delete(id).subscribe(data => {
            this.loadAllProducts(this.sort, this.page, this.amount, this.keysearch);
        });
    }

    gotoPage(){
        if(this.gopage != ""){
            this.setPage(+this.gopage);
        }else{
            this.setPage(1);
        }
    }

    goSearch(){
        this.setPage(1);
    }

}



